import React from "react";
import "../styles.css";

var a;
const calculate = () => {
  var p = parseInt(document.getElementById("p").value);
  var r = parseInt(document.getElementById("r").value);
  var t = parseInt(document.getElementById("t").value);
  // var n = parseInt(document.getElementById("n").value);
  a = Math.pow(p * (1 + r / 100), t);
  document.getElementById("A").value = a.toString();
};
const show = () => {
  return a;
};

const Ci = () => {
  return (
    <div className="formula">
      <input type="text" placeholder="P" id="p" />
      <span>(1+</span>
      <input type="text" placeholder="r" id="r" />
      <span>/</span>
      {/* <input type="text" placeholder="100" id="n" /> */}
      <span>100 )</span>
      <input type="text" placeholder="t" id="t" />
      <button onClick={calculate}> = </button>
      <input type="text" placeholder="A" id="A" value={show} />
    </div>
  );
};

export default Ci;
