import React from "react";

const redPanda = {
  src:
    "https://64.media.tumblr.com/c31c9386b4ff7b70f0544bdfc9836773/fb1f8b45ecade6d1-2d/s1280x1920/a80885de759c5c4ab962f16141279b769180dcc2.jpg",
  alt: "Red Panda",
  width: "200px"
};
const myfunc = <p>yashaysah</p>;
var s = 0;
const increase = () => {
  s = s + 1;
  document.getElementById("increase").innerHTML = s;
  return s;
};

const name = ["yash", "krish", "ramesh", "suresh"];
const names = name.map((val) => <li>{val}</li>);
const List = () => {
  return (
    <>
      <ul>{names}</ul>
      <button onClick={increase}>increment</button>
      <p id="increase">{increase}</p>
    </>
  );
};

const Try = () => {
  return (
    <div>
      <h1>Cute Red Panda</h1>
      <img src={redPanda.src} alt={redPanda.alt} width={redPanda.width} />
      <p id="yash">{myfunc}</p>
      <List />
    </div>
  );
};

export default Try;
